@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Oswald:wght@200;500&family=Ubuntu:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-app-color: #02203c;
  --secondary-app-color: #001528;
  --standout-color: #ff5823;
}
a {
  text-decoration: none !important;
}
a:hover {
  color: #ff5823 !important;
  color: var(--standout-color) !important;
}
.select-items {
  background-color: #02203c !important;
  background-color: var(--primary-app-color) !important;
}
.form-select {
  background-color: linear-gradient(
    -90deg,
    #02203c,
    #001528) !important;
  background-color: linear-gradient(
    -90deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  ) !important;
  color: #fff !important;
  font-size: 1.3rem !important;
  font-family: "Ubuntu" !important;
  border: none !important;
  margin-top: -15px !important;
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */ /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
