.btn-hire-me {
  font-size: 1.3rem;
  font-family: "Ubuntu";
  color: #fff;
  background: transparent;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.3s ease-in-out;
  -webkit-animation: Anim 1s ease-in-out infinite;
          animation: Anim 1s ease-in-out infinite;
}

.btn-hire-me:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  background-color: #ff5823;
  background-color: var(--standout-color);
  color: #1c1c1c;
}

.btn-resume-me {
  font-size: 1.3rem;
  font-family: "Ubuntu";
  color: #fff;
  background: #ff5823;
  background: var(--standout-color);
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.3s ease-in-out;
}

.btn-resume-me:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

@-webkit-keyframes Anim {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes Anim {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

