/* Mobile view styling */
@media (max-width: 750px) {
  .navbar-option-mobile {
    display: block;
    width: 100vw;
    height: 50px;
    position: fixed;
    z-index: 44;
  }
  .flex-rows {
    flex-direction: column;
    justify-content: start;
  }
  .navbar-new {
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
    -webkit-transform: translateX(700px);
            transform: translateX(700px);
  }
  .transitil-all {
    transition: all 0.3s ease-in-out;
  }
  .navbar-fixed-new {
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 50;
    /* transform: translateX(700px); */
  }
  .px-5 {
    padding-inline: 0px;
  }
  .nav-items {
    padding-block: 20px;
  }
  .brand-name {
    font-size: 1.4rem;
    padding-inline: 22px;
    margin-top: 10px;
  }
  .bars {
    height: 4px;
    width: 40px;
    background: #fff;
    float: right;
    z-index: 33;
    -webkit-transform: translateX(1px) translateY(-20px);
            transform: translateX(1px) translateY(-20px);
  }
  .bars::before {
    display: block;
    position: relative;
    top: 9px;
    content: "";
    color: #fff;
    width: 40px;
    height: 4px;
    background: #fff;
  }
  .bars::after {
    display: block;
    position: relative;
    bottom: 12px;
    content: "";
    color: #fff;
    width: 40px;
    height: 4px;
    background: #fff;
  }
  .Im-text {
    font-size: 1.2rem;
  }
  .name-Im-text {
    font-size: 20px;
  }
  .Im-text-enthusiastic {
    font-size: 2rem;
  }
  .btn-hire-me {
    font-size: 1rem;
    padding: 5px 8px;
  }
  .btn-resume-me {
    font-size: 1rem;
    padding: 5px 8px;
  }
  .custom-img {
    border: none;
  }
  .about-me-text {
    font-size: 2.7rem;
  }
  .why-text-sub {
    font-size: 1rem;
  }
  .aboutme-inside {
    width: 91%;
  }
  .lottie-1 {
    width: 500px;
  }
  .university-name {
    font-size: 1rem;
  }
  .university-degree {
    font-size: 0.9rem;
  }
  .resume-new-section {
    width: 100% !important;
    height: 100% !important;
  }
  .contact-me-card {
    height: 100% !important;
  }
  .card-contact-right {
    width: 100% !important;
    height: 100% !important;
    margin-bottom: 100%;
  }
  .test {
    margin-top: -122px;
  }
  .form-select {
    padding-block: 20px !important;
    margin-left: 14px !important;
  }
}

